import React from 'react';
import { compose, branch, renderComponent, renderNothing, lifecycle } from 'recompose';
import { connect } from 'react-redux';

import {siren, whistle} from './audio';

function HiitWorkoutOverlay({workout, reps, roundCountdown}) {
  return (
    <div className="Hiit-workout-overlay grass-pattern">
      <div className="Hiit-workoutbox Hiit-rows">
        <span className="Hiit-flash-2x">
            {workout}
          </span>
          <span className="Hiit-flash-2x">
            {reps ? `${reps} Reps` : ''}
          </span>
          <span className="Hiit-flash-3x">
            {Math.floor(roundCountdown)+1}
          </span>
      </div>
    </div>
  )
}

function FreezeScreenOverlay() {
  return (
    <div className="screen-freeze">
      <span className="Hiit-flash-3x blink"> 🏋️ </span>
    </div>
  )
}

const freezeIfChessWorkout = isFrozen =>
  branch(
    isFrozen,
    renderComponent(FreezeScreenOverlay)
  )

const hideIfOnlyChess = isHidden =>
  branch(
    isHidden,
    renderNothing
  )

const enhance = compose(
  connect(({hiit}) => ({
    isChess: hiit.isChess,
    isWorkout: hiit.isWorkout,
    workout: hiit.workout,
    reps: hiit.reps,
    roundCountdown: hiit.roundCountdown,
  })),
  hideIfOnlyChess(({isChess, isWorkout}) => isChess && !isWorkout),
  freezeIfChessWorkout(({isChess, isWorkout}) => isChess && isWorkout),
  lifecycle({
    componentDidMount() {
      whistle();
    },
    componentWillReceiveProps({roundCountdown}) {
      if (roundCountdown <= 4 && roundCountdown >= 1) {
        siren();
      }
    }
  })
)

export default enhance(HiitWorkoutOverlay)



