import React from 'react';

const DumbGame =  ({isOver, children}) => {
  const styles = `game ${isOver && 'translucent'}`
  return (
    <div className={styles}>
      {children[0]}
      {children[1]}
      {children[2]}
    </div>
  )
}

export default DumbGame



