import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { compose, branch, renderNothing, } from 'recompose';

import { localActions, appActions } from "app/actions";

import { COLOR } from 'chess/utils/constants/constants';
import { MATCH_GAMES, } from 'app/utils/games';
import FenPicker from 'app/components/common/FenPicker';
import { countFiles } from "chess/utils/fen/fen";

import DumbDashboard from "./dumb";

class SmartDashboard extends Component {

  constructor(props) {
    super(props)
    this.state = {
      isChecked: true
    };
  }

  doNew() {
    console.log("start new game")
    const { fen, newLocal } = this.props;
    newLocal(fen, this.timer);
  }

  doContinue() {
    console.log("continue workout")
    const { fen, continueLocal } = this.props;
    continueLocal(fen, this.timer);
  }

  togTimeout(e) {
    const isChecked = e.target.checked
    this.setState({
      isChecked,
    })
  }

  render() {
    const { fenIndex, setLocalFenIndex, isOver, } = this.props;
    const isChecked = this.state.isChecked;

    // timer
    const timer = (isChecked) ? (15) : (9)
    const timerLabel = `${timer}`
    this.timer = timer // passed to onNew

    const children = (
      <FenPicker fens={MATCH_GAMES}
                  select={fenIndex}
                  setFenIndex={setLocalFenIndex} />
    )

    const renderProps = {
      orientation: COLOR.WHITE,
      timer,
      timerLabel,
      isChecked,
      isOver,
      onNew: this.doNew.bind(this),
      onContinue: this.doContinue.bind(this),
      togTimeout: this.togTimeout.bind(this),
    }

    return (
      <DumbDashboard {...renderProps}>
        {children}
      </DumbDashboard>
    )
  }

}

const mapStateToProps = ({ local }) => {
  const { fenIndex } = local;
  const fen = MATCH_GAMES[fenIndex];
  const files = countFiles(fen)

  //const { orientation } = app;
  return {
    fenIndex,
    fen,
    files,
    //orientation,
  }
}

const mapDispatchToProps = dispatch => bindActionCreators({
  ...localActions,
  ...appActions,
}, dispatch)

const hideIfNoData = hasNoData =>
  branch(
    hasNoData,
    renderNothing
  )

const enhance = compose(
  hideIfNoData(({isActive}) => isActive),
  connect(mapStateToProps, mapDispatchToProps),
)

export default enhance(SmartDashboard)




