import React from 'react'
import { connect } from 'react-redux'

import Dashboard from './dashboard'
import Game from './game'

import {
  START_GAME,
  END_GAME,
} from 'app/actions/constants'

const Local = ({isActive, isOver, hasFen}) => (
  <div>
    <Game isOver={isOver} hasFen={hasFen} />
    <Dashboard isOver={isOver} isActive={isActive} />
  </div>
)

const mapStateToProps = ({ local, user, chess }, ) => {
  const isLoggedin = user.isLoggedin
  const progress = local.progress
  const hasFen = !!chess.fen
  return {
    isLoggedin,
    isActive:  progress === START_GAME,
    isOver: progress === END_GAME,
    hasFen,
  }
}

export default connect(mapStateToProps, null)(Local)



